<template>
	<view class="content">
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				text:''
			}
		},
		onLoad() {

		},
		methods: {
			put(){
				uni.request({
				    url: 'http:///127.0.0.1:8080/home', //仅为示例，并非真实接口地址。
				    data: {
				        name:'黄怡',
						gender:'女'
				    },
				    header: {
				        'custom-header': 'hello' //自定义请求头信息
				    },
				    success: (res) => {
						console.log('成功请求到了数据，数据如下');
				        console.log(res.data);
				        this.text = res.data.data
				    }
				});
			}
		},
		beforeCreate() {
			uni.getStorage({
				key: 'user',
				success: function (res) {
					if(res.data===null) uni.redirectTo({
						url: './login'
					});
				}
			});
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
